<template>
  <b-navbar class="HeadNav">
    <router-link to="/">
      <img src="@/assets/images/logo.png" />
    </router-link>
    <div v-if="userInfo" class="ml-auto">
      <b-row class="d-flex justify-content-end">
        <div class="m-2">
          <span
            class="mr-2 text-style font-weight-bold"
            v-if="nowTime != null"
          >{{ nowTime | moment("YYYY-MM-DD HH:mm:ss")}}</span>
          <span
            class="text-style font-weight-bold"
            v-if="userInfo && !isVisitor"
          >({{ userInfo.emp_ID }}){{ userInfo.emp_name }}</span>
          <span
            class="text-style font-weight-bold"
            v-if="userInfo && isVisitor"
          >(visitor)</span>
        </div>
      </b-row>

      <b-row>
        <b-navbar-nav class="ml-auto">
          <b-dropdown
            v-if="PermissionParse(userInfo.profile_permission)"
            variant="primary"
            no-caret
            text=""
            right
            class="m-2"
          >
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/driver"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/vehicle"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/route"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/driver-leave"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/national-holiday"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/profile-management/special-date"></b-dropdown-item>
          </b-dropdown>

          <b-dropdown
            v-if="PermissionParse(userInfo.schedule_permission)"
            variant="primary"
            no-caret
            text=""
            right
            class="m-2"
          >
            <b-dropdown-item class="hover-dropdown-item" to="/schedule-management/career"></b-dropdown-item>
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/schedule-management/general-schedule"
            ></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/schedule-management/schedule"></b-dropdown-item>
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/schedule-management/schedule-query"
            ></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/schedule-management/working-hours"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/schedule-management/distance-adjust"></b-dropdown-item>
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/schedule-management/schedule-related-reports"
            ></b-dropdown-item>
          </b-dropdown>

          <b-dropdown
            v-if="PermissionParse(userInfo.salary_permission)"
            variant="primary"
            no-caret
            text=""
            right
            class="m-2"
          >
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/salary-management/basic-salary-parameter"
            ></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/salary-management/payroll"></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" to="/salary-management/payroll-query"></b-dropdown-item>
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/salary-management/salary-statement"
            ></b-dropdown-item>
          </b-dropdown>

          <b-dropdown variant="primary" no-caret text="" right class="m-2">
            <b-dropdown-item
              class="hover-dropdown-item"
              to="/system-configuration/personal-password-setting"
            ></b-dropdown-item>
            <b-dropdown-item
              v-if="isAdmin(userInfo)"
              class="hover-dropdown-item"
              to="/system-configuration/account-management"
            ></b-dropdown-item>
            <b-dropdown-item
              v-if="isAdmin(userInfo)"
              class="hover-dropdown-item"
              to="/system-configuration/transfer-voucher"
            ></b-dropdown-item>
            <b-dropdown-item
              v-if="isAdmin(userInfo)"
              class="hover-dropdown-item"
              to="/system-configuration/operation-record-query"
            ></b-dropdown-item>
            <b-dropdown-item class="hover-dropdown-item" @click="SignOut()"></b-dropdown-item>
          </b-dropdown>
        </b-navbar-nav>
      </b-row>
    </div>
  </b-navbar>
</template>

<script>
import { mapState } from 'vuex';
import Logout from '@/layout/Logout';

export default {
  name: 'HeadNav',
  data() {
    return {
      nowTime: null,
      isVisitor: false,
    };
  },
  mounted() {
    window.setInterval(() => {
      this.nowTime = new Date();
    }, 1000);
    const member = window.sessionStorage.getItem('member');
    if (member === 'visitor') {
      this.isVisitor = true;
    } else {
      this.isVisitor = false;
    }
  },
  computed: {
    ...mapState(['userInfo']),
    routes() {
      return this.$router.options.routes;
    },
  },
  methods: {
    PermissionParse(permission) {
      const ForbidPermission = 'X';
      return permission !== ForbidPermission;
    },
    isAdmin(userInfo) {
      return userInfo.emp_ID === 'admin' && userInfo.title === '';
    },
    SignOut() {
      Logout.call(this);
    },
  },
};
</script>

<style lang="scss" scoped>
.HeadNav {
  border-bottom: 1px #194c28 solid;
  box-shadow: 0px 0px 20px rgba(22, 76, 40, 0.2);
}

.text-style {
  color: #1c4a9d;
}

.hover-dropdown-item {
  :hover {
    color: white;
    background-color: #0dac67;
  }
}
</style>
