<template>
  <div class="d-flex flex-column flex-grow-1" ref="tableRoot">
    <b-table-simple
      class="m-0 text-nowrap"
      ref="tableInfo"
      bordered
      responsive
      no-border-collapse
      thead-tr-class="text-center"
      :sticky-header="`${tableHeight}px`"
    >
      <b-thead>
        <b-tr>
          <b-th class="text-center" sticky-column></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
          <b-th class="text-center"></b-th>
        </b-tr>
      </b-thead>
      <b-tbody>
        <template v-for="(item, index) in pageTable">
          <b-tr :key="index">
            <b-td class="text-center" sticky-column>{{ item. }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
            <b-td class="text-right">{{ item. | thousandsComma }}</b-td>
          </b-tr>
        </template>
      </b-tbody>
      <b-tfoot>
        <b-tr>
          <b-td variant="secondary" class="text-right" sticky-column></b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
          <b-td variant="secondary" class="text-right">{{ pageTable.reduce((a, b) => a + b[''], 0) | thousandsComma }}</b-td>
        </b-tr>
      </b-tfoot>
    </b-table-simple>

    <div class="d-flex justify-content-center p-2" ref="tablePagination">
      <b-pagination-nav
        pills
        use-router
        v-model="tableSettings.currentPage"
        :link-gen="linkGen"
        :number-of-pages="pageAmount"
        first-text=""
        prev-text=""
        next-text=""
        last-text=""
        class="m-0"
      >
      </b-pagination-nav>
    </div>
  </div>
</template>

<script>
export default {
  name: 'WithholdingReportTable',
  props: {
    Table: Object,
    containerHeight: Number,
  },
  data() {
    return {
      isMounted: false,
      tableSettings: {
        perPage: 13,
        currentPage: 1,
        baseHeight: 200,
      },
      pageContent: [],
    };
  },
  mounted() {
    this.isMounted = true;
  },
  filters: {
    thousandsComma(value) {
      const n = Number(value);
      const res = n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
      return res === '0' ? '' : res;
    },
    thousandsCommaOrHyphen(value) {
      const n = Number(value);
      if (n === 0) return '-';
      return n.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ',');
    },
  },
  watch: {
    Table: {
      handler() {
        this.tableSettings.currentPage = 1;
        this.PageContent();
      },
      deep: true,
    },
  },
  computed: {
    tableHeight() {
      if (!this.isMounted) return this.tableSettings.baseHeight;

      const Pagination = this.$refs.tablePagination.clientHeight;

      const res = this.containerHeight - Pagination - 90;

      return res > this.tableSettings.baseHeight
        ? res
        : this.tableSettings.baseHeight;
    },
    pageAmount() {
      return this.pageContent.length || 1;
    },
    pageTable() {
      return this.pageContent[this.tableSettings.currentPage - 1] || [];
    },
  },
  methods: {
    PageContent() {
      const data = JSON.parse(JSON.stringify(this.Table.items));

      if (!data) {
        this.pageContent = [];
        return;
      }

      //  ['', '', ''] 
      // 
      const result = [];

      // 
      //  '', '', '', '' seq -4 ~ -1;
      const tour = data.filter(item => item.dept === '');

      tour.forEach((item) => {
        const temp = item;
        if (temp. === '') temp.seq = -4;
        if (temp. === '') temp.seq = -3;
        if (temp. === '') temp.seq = -2;
        if (temp. === '') temp.seq = -1;
      });

      tour.sort((a, b) => {
        const res = a.seq - b.seq;
        return res || 0;
      });

      // 13
      for (let i = 0, len = tour.length; i < len; i += 13) {
        result.push(tour.slice(i, i + 13));
      }

      // 
      const shuttle = data.filter(item => item.dept === '')
        .sort((a, b) => {
          const res = a.seq - b.seq;
          return res || 0;
        });

      for (let i = 0, len = shuttle.length; i < len; i += 13) {
        result.push(shuttle.slice(i, i + 13));
      }

      // 
      const contract = data.filter(item => item.dept === '')
        .sort((a, b) => {
          const res = a.seq - b.seq;
          return res || 0;
        });

      for (let i = 0, len = contract.length; i < len; i += 13) {
        result.push(contract.slice(i, i + 13));
      }

      this.pageContent = result;
    },
    linkGen() {
      return { path: '/salary-management/salary-statement' };
    },
  },
};
</script>

<style lang="scss" scoped>
.fix-sm-height {
  height: 26px;
}

.no-border {
  border: none;
}

.column-sticky-right {
  position: sticky;
  top: 0;
  z-index: 2;
